import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginPage from '../components/LoginPage'
import HomePage from '../components/HomePage'
import ReguserPage from '../components/ReguserPage'
import UserHome from '../components/UserHome'
import UserMessage from '../components/user/UserMessage.vue'
import UserSpace from '../components/user/UserSpace.vue'
import UserHom from '../components/user/UserHome'
import VipUser from '../components/user/VipUser'
import UpData from '../components/user/UptData'
import Usershow from '../components/UserShow'
import MymustThing from '../components/MymustThing'
import UserCart from '../components/UserCart'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  {
    path: '/login',
    component: LoginPage
  },
  { path: '/home', component: HomePage },
  { path: '/urlshow', component: Usershow },
  { path: '/reguser', component: ReguserPage },
  { path: '/mymust', component: MymustThing },
  { path: '/usercart', component: UserCart },
  // 路由对应关系  子路由不需要 /
  {
    path: '/userhome',
    component: UserHome,
    // 默认显示子路由
    redirect: '/userhome/user',
    children: [
      { path: 'usermessage', component: UserMessage },
      { path: 'userspace', component: UserSpace },
      { path: 'user', component: UserHom },
      { path: 'vipuser', component: VipUser },
      { path: 'updata', component: UpData }]
  }
]

const router = new VueRouter({
  routes
})
// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
// to 将要访问的路径
// from 代表从那个路径跳转过来的
// 放行 next
  if (to.path === '/login') return next()
  if (to.path === '/reguser') return next()
  // 获取token
  const tokenstr = window.sessionStorage.getItem('token')
  // 为空说明没有登录
  if (!tokenstr) return next('/login')
  // else 说明登录
  next()
})

export default router
