  </div>
  <div class="jiugonge">
    <div class="userbox">
      <div class="imgbox">
        <div class="userimg" v-on:mousemove="showuser" v-bind:style="{display:bt}" v-on:mouseleave="hident">{{userimg}}</div>
        <div class="userhome" v-bind:style="{display:sh}" v-on:mousemove="showuser"  v-on:mouseleave="hident">
          <div class="homeuser">{{userimg}}</div>
          <div class="homenick">欢迎11--{{$store.state.usermessage.nickname}}</div>
          <!-- {{userinfo.nickname}} -->
