<script>
import bus from '../components/eventbus'
export default {
  data () {
    return {
      swiperimgarr: [{ img: '../assets/swiperimg/1-1.jpg' },
        { img: '../assets/swiperimg/1-2.jpg' },
        { img: '../assets/swiperimg/1-3.jpg' }],
      // 用户信息
      userinfo: {
        username: '',
        nickname: '',
        user_pic: ''
      },
      // 存储昵称作为头像
      userimg: '',
      sh: 'none',
      bt: ''
    }
  },
  methods: {
    //, 鼠标移动到头像显示个人
    showuser () {
      this.sh = 'block'
      this.bt = 'none'
    },
    // 离开
    hident () {
      this.sh = 'none'
      this.bt = 'block'
    },
    async getuser () {
      // store 用户信息
      this.userinfo = this.$store.state.usermessage
      const num = this.userinfo.nickname
      if (num === '') {
        const str = this.userinfo.username
        this.userimg = str.slice(0, 1)
      } else {
        const str = this.userinfo.nickname
        this.userimg = str.slice(0, 1)
      }
      // 发送数据给UserNick组件
      bus.$emit('share', '10')
    },
    // 退出功能
    loginout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    }
  },
  created () {
    this.getuser()
  }
}
</script>

