<template>
 <div class="box">
<div class="loginbox" >
 <div class="formbox">
  <div class="formtitle">
    <h4>账户登录</h4>
  </div>
  <div class="usernamebox">
    <img src="../assets/log/usernameimg.png" alt="" style="width:30px;height:30px">
    <input type="text" v-model="fordata.username" @blur="namejwt">
    <span class="userjwt">{{tipname}}</span>
  </div>
  <div class="pwdbox">
    <img src="../assets/log/pwdimg.png" alt="" style="width:30px;height:30px">
    <input type="password" v-model="fordata.password" @blur="pwdjwt">
    <span class="pwtjwt">{{tippwd}}</span>
  </div>
  <div class="btn">
    <button @click="logsubmit">登录</button>
  </div>
 </div>

 <div class="regbox">
  <div class="regtitle"><h4>你好，朋友！</h4></div>
  <div class="regtext">
    <h6>开始注册，和我一起吧！</h6>
  </div>
  <div class="regbtn">
    <button><router-link to="/reguser">注册</router-link> </button>
  </div>
 </div>
</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      fordata: {
        username: '',
        password: ''
      },
      tipname: '',
      tippwd: ''
    }
  },
  methods: {
    async logsubmit () {
      // 用户名和密码为空就不发送请求
      if (this.fordata.username === '') return
      if (this.fordata.password === '') return
      const { data: res } = await this.$http.post('/api/login', this.fordata)
      // console.log(res)
      if (res.status !== 0) return
      // // // 保存token有权限访问
      window.sessionStorage.setItem('token', res.token)
      // 页面跳转
      this.$router.push('/home')
      // 使用路由导航页面控制页面的跳
    },
    namejwt () {
      const name = this.fordata.username
      const uPattern = /^[a-zA-Z0-9]{4,16}$/
      const jwtt = uPattern.test(name)
      if (jwtt !== true) { this.tipname = '*' } else {
        this.tipname = ''
      }
    },
    pwdjwt () {
      const name = this.fordata.password
      const uPattern = /^[0-9]{5,16}$/
      const jwtt = uPattern.test(name)
      if (jwtt !== true) { this.tippwd = '*' } else {
        this.tippwd = ''
      }
    }
  }
}
</script>

<style  scoped>
.loginbox{
   height: 300px;
   width: 600px;
   background-color: rgb(95,71,154);
   margin: 150px 20% 200px 20%;
   border-radius: 10px;
   display: flex;
}
.formtitle{
width: 100px;
height: 40px;
line-height: 50px;
color: rgb(246, 172, 25);
margin: 0 0 20px 100px;
text-align: center;
}
.formbox{
  margin-right: 14%;
}
.usernamebox ,.pwdbox{
width: 240px;
height: 40px;
background-color: rgb(246, 172, 25);
border-radius: 20px;
margin: 10px 0px 30px 100px;
display: flex;
}
.usernamebox input,.pwdbox input{
  margin: 8px 0px 0px 3px;
  width: 180px;
  height: 23px;
  background-color: rgb(247, 224, 179);
  border: 0ch;
  outline: none;
  font-size: 16px;
}
.usernamebox img,.pwdbox img{
margin: 4px 2px 2px 2px;
}
.btn{
  margin-left: 60%;
}
.btn button{
  width: 50px;
  height: 30px;
  border-radius: 10px;
}
.regbox{
  width: 260px;
  height: 100%;
  background-color: rgb(246,172,25);
  border-radius: 0px 10px 10px 0px;
}
.regtitle{
  margin: 60px 0px 20px 50px;
}
.regtext{
  margin: 0px 0px 20px 30px;
}
.regbtn{
margin:40px 0px 0px 50px ;
}
.regbtn button{
  width: 40px;
  height: 30px;
  border-radius: 20px;
}
.userjwt,.pwtjwt{
  margin: 9px 0px 0px 5px;
  color: red;
  font-size: 22px;
}
</style>
