<template>
  <div class="usercart-box">
    <div class="carttop">
    <h3>欢迎--{{$store.state.usermessage.nickname}}--来到我的购物车</h3>
    </div>
    <div class="cartnav">
      <ul v-for="(item,index) in cartdata" :key="index">
        <li >{{item.name}}|</li>
        <li>商品价格{{item.price}}|</li>
        <li>商品总价{{item.totlprice}}|</li>
        <li>商品数量{{item.count}}<button @click="addcount($event,item.count,index,item.id)">添加数量</button></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      cartdata: {}
    }
  },
  methods: {
    async getcartdata () {
      // 查询用户的购物车数据渲染到页面
      const { data: res } = await this.$http.get('/usercart/car ')
      this.cartdata = res.data
    },
    // number 商品当前的数量  ，index 当前商品的索引，id商品的id
    async addcount (e, number, index, id) {
      // 拿到点击时候的索引 将count数量改变  最后将改变的数量传递到数据库
      this.cartdata[index].count += 1

      const num = this.cartdata[index].count
      const pric = this.cartdata[index].price
      // console.log('价格' + pric + '数量' + num)
      const totalprice = parseInt(pric) * parseInt(num)
      this.cartdata[index].totlprice = totalprice
      console.log()
      await this.$http.post('/usercart/upcount', { count: num, id: id, totlprice: totalprice })
    //  const { data: res } =
      // console.log(res)
    }
  },
  created () {
    this.getcartdata()
  }
}
</script>

<style scoped>
.cartnav ul{
  display: flex;
}
.cartnav ul li{
  margin: 20px 0px 5px 20px;
}
</style>
