<template>
  <div class="timbox">
    <div class="timouttitle"><h2>勾勾秒杀</h2> </div>
    <div class="timoutimg"></div>
    <div class="timetip">16:00 点场--距离结束 </div>
    <div class="timebox">
      <div class="timone">
        {{day}}
      </div><span>:</span>
      <div class="timtwo">
        {{hour}}
      </div><span>:</span>
      <div class="timthree">
        {{min}}
      </div><span>:</span>
      <div class="timforce">
        {{second}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      curStartTime: '2022-10-10 18:00:00',
      day: '',
      hour: '',
      min: '',
      second: ''
    }
  },
  methods: {
    // 倒计时
    countTime () {
      // 获取当前时间
      const date = new Date()
      const now = date.getTime()
      // 设置截止时间
      const endDate = new Date(this.curStartTime) // this.curStartTime需要倒计时的日期
      const end = endDate.getTime()
      // 时间差
      const leftTime = (end - now) / 1000
      const d = Math.floor(leftTime / 60 / 60 / 24)
      this.day = d < 10 ? '0' + d : d
      const h = Math.floor(leftTime / 60 / 60 % 24)
      this.hour = h < 10 ? '0' + h : h
      const m = Math.floor(leftTime / 60 % 60)
      this.min = m < 10 ? '0' + m : m
      const s = Math.floor(leftTime % 60)
      this.second = s < 10 ? '0' + s : s
      // console.log(this.day + '天', this.houer + '时', this.min + '分', this.second + '秒')
    }
  },
  created () {
    this.countTime()
  }
}
</script>

<style>
.timbox{
  width: 240px;
  height: 360px;
  border-radius: 10px;
  margin-left: 30px;
  background: rgb(241, 78, 78);
}
.timouttitle{
  height: 80px;
  margin: 30px 20px 0px 20px;
  line-height: 80px;
  text-align: center;
  color: rgb(255, 255, 255);
}
.timoutimg{
  height: 100px;
  margin: 0px 20px 0px 20px;
  background-color: aqua;
}
.timetip{
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 18px;
}
.timebox span{
  margin: 10px;
  font-size: 20px;
  color: rgb(255, 255, 255);
}
.timebox{
  margin: 10px 10px 0px 15px;
  display: flex;
  justify-content: space-around;
}
.timebox .timone ,.timtwo ,.timthree,.timforce{
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: black;
  color: aliceblue;
}
</style>
