// eslint-disable-next-line vue/multi-word-component-names
<template>
  <div class="showbox">
    <div class="topbox">
      <div class="topboxt-itle">常用网址导航</div>
      <div class="topbox-add" @click="zahnshi">::+::</div>
    </div>
    <div class="addurl" v-bind:style="{display:hidensh}" @mouseleave="show">
     <div class="addurl-title">添加网站</div>
     <div class="addurl-nav">
      <ul>
        <li>最常访问</li>
        <li>当前打开</li>
        <li>手动添加</li>
      </ul>
     </div>
     <!--  -->
     <div class="addinput">
      <div>将网站添加到*</div>
      <div></div>
     </div>
     <!--  -->
     <div class="addinput">
      <div>名称*</div>
      <div><input type="text" v-model="urlcan.urlname"></div>
     </div>
     <!--  -->
     <div class="addinput">
      <div>链接*</div>
      <div><input type="text" v-model="urlcan.urladres"></div>
     </div>
     <div class="addinput">
      <button @click="addurl">添加</button>
     </div>
    </div>
    <!--  -->
    <div class="showurl-box">
      <ul>
        <li v-for="(item,index) in geturldata " :key="index">
          <div class="abox"><a :href="item.urladres">{{item.urlname.slice(0,1)}}</a></div>
          <div class="bbox"><a :href="item.urladres">{{item.urlname}}</a></div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      urlcan: {
        urlname: '',
        urladres: ''
      },
      hidensh: 'none',
      geturldata: {}
    }
  },
  methods: {
    async addurl () {
      if (this.urlcan.urlname === '') return
      if (this.urlcan.urladres === '') return
      const { data: res } = await this.$http.post('/url/urladres', this.urlcan)
      console.log(res)
      this.hidensh = 'none'
    },
    zahnshi () {
      this.hidensh = 'block'
    },
    show () {
      this.hidensh = 'none'
    },
    // 获取用户收藏的网址
    async geturl () {
      const { data: res } = await this.$http.get('/url/geturl')
      this.geturldata = res.data
    },
    // 头像函数
    topimg () {

    }
  },
  created () {
    this.geturl()
  }
}
</script>

<style scoped>
.showbox{
  height: 600px;
  margin: 0px 200px 0px 200px;
  background-color: rgb(150, 161, 157);
}
.topbox{
  height: 80px;
  background-color: aquamarine;
  display: flex;
}
.topbox .topboxt-itle ,.topbox-add{
  margin: 20px 10px 0px 50px;
}
.topbox .topboxt-itle{
  width: 120px;
  height: 40px;
  line-height:40px ;
  text-align: center;
}
.topbox .topbox-add{
  width: 40px;
  height: 40px;
  line-height:40px ;
  text-align: center;
  margin-left: 70%;
font-size: 20px;
color: brown;
}
.topbox-add:hover{
  font-size:28px ;
  color: coral;
  cursor: pointer;
}
.addurl{
width: 300px;
height: 400px;
background-color: bisque;
border-radius: 10px;
position: absolute;
top: 80px;
right: 120px;
}
.addurl-title{
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 26px;
}
.addurl .addurl-nav ul{
display: flex;
}
.addurl .addurl-nav ul li{
  margin:10px 0px 10px 20px ;
}
.addinput{
  height: 50px;
  margin-left: 20px;
}
.addinput button{
  width: 46px;
  height: 28px;
  margin: 20px 0px 0px 100px;
}
.addinput input{
  height: 20px;
  border-radius: 10px;
}
.showurl-box ul{
  display: flex;
  justify-content:space-between;
  flex-wrap: wrap;
}
.showurl-box ul li{
  width: 200px;
  height: 200px;
  margin-top: 20px;
}
.showurl-box ul li .abox{
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 30px;
  border-radius:50% ;
  margin: 29px 0px 0px 50px;
  background-color: aqua;
}
.bbox{
  width:120px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 24px;
  margin: 29px 0px 0px 30px;
}
.bbox:hover{
  font-size: 30px;
}
</style>
