<template>
  <div>
    <div class="loginbox" >
      <div class="regbox">
        <div class="regtitle"><h4>你好，朋友！</h4></div>
        <div class="regtext">
        <h6>注册成功直接跳转到登录页面</h6>
        </div>
        <div class="regtext">
          <h6>嘿嘿忘记注册过，突然想起已注册点击这里</h6>
          </div>
          <div class="regbtn">
            <button><router-link to="/login">登录</router-link> </button>
          </div>
       </div>
      <div class="formbox">
       <div class="formtitle">
         <h4>账户登录</h4>
       </div>
       <div class="usernamebox">
         <img src="../assets/log/usernameimg.png" alt="" style="width:30px;height:30px">
         <input type="text" v-model="fordata.username" @blur="namejwt">
       <span class="userjwt">{{ tipname}}</span>
        </div>
       <div class="pwdbox">
         <img src="../assets/log/pwdimg.png" alt="" style="width:30px;height:30px">
         <input type="password" v-model="fordata.password" @blur="pwdjwt">
      <span class="pwtjwt">{{tippwd}}</span>
        </div>
       <div class="btn">
        <!-- @click="logsubmit" -->
         <button @click="regin">注册</button>
       </div>
      </div>
     </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      fordata: {
        username: '',
        password: ''
      },
      tipname: '',
      tippwd: ''
    }
  },
  methods: {
    async regin () {
      if (this.fordata.password === '') return
      if (this.fordata.username === '') return
      const { data: res } = await this.$http.post('/api/reguser', this.fordata)
      if (res.status === 1) return console.log('注册失败')
      //  保存token有权限访问
      window.sessionStorage.setItem('token', res.token)
      // // // 页面跳转
      this.$router.push('/login')
      // // 使用路由导航页面控制页面的跳
    },
    namejwt () {
      const name = this.fordata.username
      const uPattern = /^[a-zA-Z0-9]{4,16}$/
      const jwtt = uPattern.test(name)
      if (jwtt !== true) { this.tipname = '*' } else {
        this.tipname = ''
      }
    },
    pwdjwt () {
      const name = this.fordata.password
      const uPattern = /^[0-9]{6,16}$/
      const jwtt = uPattern.test(name)
      if (jwtt !== true) { this.tippwd = '*' } else {
        this.tippwd = ''
      }
    }
  }
}
</script>

<style scoped>
  .loginbox{
    height: 300px;
    width: 600px;
    background-color: rgb(229,168,75);
    margin: 150px 20% 200px 20%;
    border-radius: 10px;
    display: flex;
 }
 .formtitle{
 width: 100px;
 height: 40px;
 line-height: 50px;
 margin: 0 0 20px 100px;
 text-align: center;
 color: rgb(28,141,108);
 }
 .formbox{
   margin-right: 14%;
 }
 .usernamebox ,.pwdbox{
 width: 240px;
 height: 40px;
 background-color: rgb(28,141,108);
 border-radius: 20px;
 margin: 10px 0px 30px 100px;
 display: flex;
 }
 .usernamebox input,.pwdbox input{
   margin: 8px 0px 0px 3px;
   width: 180px;
   height: 23px;
   background-color: aquamarine;
   border: 0ch;
   outline: none;
   font-size: 16px;
 }
 .usernamebox img,.pwdbox img{
 margin: 4px 2px 2px 2px;
 }
 .btn{
   margin-left: 60%;
 }
.btn button{
    width: 40px;
    height: 30px;
    border-radius: 20px;
  }
 .regbox{
   width: 260px;
   height: 100%;
   background-color:rgb(28,141,108);
   border-radius: 10px 0px 0px 10px;
 }
 .regtitle{
   margin: 60px 0px 20px 50px;
   color: rgb(229,168,75);
 }
 .regtext{
   margin: 0px 0px 20px 10px;
   color: rgb(229,168,75);
 }
 .regbtn{
  margin:40px 0px 0px 50px ;
  }
  .regbtn button{
    width: 40px;
    height: 30px;
    border-radius: 20px;
  }
 .userjwt,.pwtjwt{
  margin: 9px 0px 0px 5px;
  color: red;
  font-size: 22px;
}
</style>
